.weapon-where-box {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.064), 0 3px 6px rgba(0, 0, 0, 0.092);
  background: #f8f9fa;
  width: 1024px;
  margin: 0 auto;
  margin-bottom: 1.5rem;
  padding: 1rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 1rem; }
  @media (max-width: 1399px) {
    .weapon-where-box {
      width: 768px; } }
  @media (max-width: 1023px) {
    .weapon-where-box {
      width: 512px; } }
  @media (max-width: 767px) {
    .weapon-where-box {
      width: 100%; } }
  .weapon-where-box .cl-where .clyn {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 0.5rem; }
    @media (max-width: 1023px) {
      .weapon-where-box .cl-where .clyn {
        grid-template-columns: repeat(4, 1fr); } }
  .weapon-where-box .weapon-illegal-where .illegal {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 0.5rem; }
    @media (max-width: 1023px) {
      .weapon-where-box .weapon-illegal-where .illegal {
        grid-template-columns: repeat(4, 1fr); } }
  .weapon-where-box .weapon-type-where {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 0.5rem; }
    .weapon-where-box .weapon-type-where .weapon-type1 {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      grid-gap: 0.5rem; }
      @media (max-width: 1023px) {
        .weapon-where-box .weapon-type-where .weapon-type1 {
          grid-template-columns: repeat(4, 1fr); } }
    .weapon-where-box .weapon-type-where .weapon-type2 {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      grid-gap: 0.5rem; }
      @media (max-width: 1023px) {
        .weapon-where-box .weapon-type-where .weapon-type2 {
          grid-template-columns: repeat(4, 1fr); } }
  .weapon-where-box .grade-where {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 0.5rem; }
    @media (max-width: 1023px) {
      .weapon-where-box .grade-where {
        grid-template-columns: repeat(4, 1fr); } }
