.weapon-view-powerd-by {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between; }
  .weapon-view-powerd-by .powered-by-dmg-list {
    width: 100px; }
    .weapon-view-powerd-by .powered-by-dmg-list .powered-by-dmg-title {
      padding-bottom: 0.4rem;
      margin-bottom: 0.2rem;
      border-bottom: 1px solid #ced4da;
      font-weight: bold; }
    .weapon-view-powerd-by .powered-by-dmg-list .dmg {
      cursor: pointer;
      color: #ffa8a8;
      font-size: 1rem;
      margin-bottom: 0.3rem; }
      .weapon-view-powerd-by .powered-by-dmg-list .dmg .powered-by-tit {
        float: right;
        display: none; }
    .weapon-view-powerd-by .powered-by-dmg-list .dmg:hover {
      color: #c92a2a;
      font-weight: bold; }
      .weapon-view-powerd-by .powered-by-dmg-list .dmg:hover .powered-by-tit {
        display: inline; }
    .weapon-view-powerd-by .powered-by-dmg-list .dmg:active {
      color: #c92a2a;
      font-weight: bold; }
      .weapon-view-powerd-by .powered-by-dmg-list .dmg:active .powered-by-tit {
        display: inline; }
