.list-wrapper {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.064), 0 3px 6px rgba(0, 0, 0, 0.092);
  width: 1024px;
  margin: 0 auto;
  overflow-y: auto;
  margin-bottom: 1.5rem;
  padding: 1rem; }
  @media (max-width: 1399px) {
    .list-wrapper {
      width: 768px; } }
  @media (max-width: 1023px) {
    .list-wrapper {
      width: 512px; } }
  @media (max-width: 767px) {
    .list-wrapper {
      width: 100%; } }
